import {Icon} from 'src/components';
import {quickSort} from '@ra-lib/util';

/**
 *  id + parentId  id + children
 * 
 *  id
 *  parentId  children
 *  title
 *  path
 *
 * @param userId
 * @returns {Promise<[{
 * id: string,
 * parentId: string,
 * icon: string,
 * title: string,
 * basePath: string,
 * path: string,
 * target: string,
 * order: number,
 * }]>}
 * @description
 *  basePath: 
 *  path: 
 *  target: iframeiframe
 *  order: 
 */
export default async function getMenus(userId) {
    // userId  token 
    // const menus = await ajax.get('/menus', {userId}).then(res => {
    //     return (res || []);
    // });

    const menus = [
        {id: 'system', title: '', order: 900},
        {id: 'user', parentId: 'system', title: '', path: '/users', order: 900},
        {id: 'role', parentId: 'system', title: '', path: '/roles', order: 900},
        {id: 'menu', parentId: 'system', title: '', path: '/menus', order: 900},

        {id: 'demo', title: '', basePath: '/demo', order: 850},
        {id: 'page404', parentId: 'demo', title: '404', path: '/404', order: 700},
        {id: 'layout', parentId: 'demo', title: '', path: '/layout', order: 900},
        {id: 'modal', parentId: 'demo', title: '', path: '/modal', order: 900},
        {id: 'query-bar', parentId: 'demo', title: '', path: '/query-bar', order: 900},

        {id: 'other-site', title: '', order: 800},
        {id: 'baidu', parentId: 'other-site', title: '', path: 'https://baidu.com', target: 'iframe', order: 2000},
        {id: 'baidu-zhidao', parentId: 'other-site', title: '', path: 'https://zhidao.baidu.com', target: 'iframe', order: 2000},
        {id: 'error', parentId: 'other-site', title: '', path: 'https://aaa.baidu.com', target: 'iframe', order: 2000},
        {id: 'antDesign', parentId: 'other-site', title: 'Ant Design ', path: 'https://ant.design', target: 'iframe', order: 2000},
        {id: 'document', parentId: 'other-site', title: '', path: 'https://sxfad.github.io/react-admin/#/', target: '_blank', order: 1200},
    ];

    return loopMenus(menus);
}

/**
 * {}
 * @param nodes
 * @param basePath
 */
function loopMenus(nodes, basePath) {
    //  order 
    nodes = quickSort(nodes, (a, b) => b.order - a.order);

    nodes.forEach(item => {
        let itemBasePath = basePath;

        let {icon, path, target, children} = item;

        // basePath
        const parentNode = nodes.find(it => it.id === item.parentId);
        if (parentNode?.basePath) itemBasePath = parentNode.basePath;

        // bashPath
        if (itemBasePath && !('basePath' in item)) item.basePath = itemBasePath;

        // 
        if (itemBasePath && path && (!path.startsWith('http') || !path.startsWith('//'))) {
            item.path = path = `${itemBasePath}${path}`;
        }

        // 
        if (icon) item.icon = <Icon type={icon}/>;

        // targetiframe
        if (target === 'iframe') {
            //  : iFrame
            item.path = `/iframe_page_/${encodeURIComponent(path)}`;
            Reflect.deleteProperty(item, 'target');
        }

        if (children?.length) loopMenus(children, item.basePath);
    });

    return nodes;
}
